import {Col, Container, Row} from "react-bootstrap";
import React from "react";


export function Banner(params) {
    return (
       <Container className="banner">
           <Row className="align-items-center">
               <Col md={6}>
                   <div>
                       <h1>Wanna get Pissed?</h1>
                       <h5>You won't be disappointed</h5>
                       <button className="bookSlot">Book Slot Now</button>
                   </div>
               </Col>

               <Col md={6}>
                   <div className="">
                       <img className="img-fluid" src="https://api.thegrowingdeveloper.org/assets/img/home-api.jpg"/>
                   </div>
               </Col>
           </Row>
       </Container>
    );
}