import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGithub,
  faFacebookF,
  faTwitter,
  faYoutube,
  faLinkedinIn,
} from "@fortawesome/free-brands-svg-icons";
import TGDdark from "./images/TGDdark.png";

export function Footer() {
  return (
    <div className="footer">
      <Container>
        <div className="d-flex justify-content-start align-items-start">
          <img
            className="img-fluid footer-logo mt-1 me-3"
            src={TGDdark}
            alt=""
          />
          <div className="footer-brandName">
            <h3>The Growing Developer</h3>
            <p>"Let's learn and Grow TOGETHER"</p>
          </div>
        </div>

        <hr />

        <div className="social d-lg-flex justify-content-between text-center">
          <p>
            © Copyright <span>The Growing Developer</span>. All Rights Reserved
          </p>
          <div>
            <a
              className="social-icons"
              href="https://twitter.com/thegrowingdev"
            >
              <FontAwesomeIcon icon={faTwitter} />
            </a>

            <a
              className="social-icons"
              href="https://www.facebook.com/thegrowingdeveloper/"
            >
              <FontAwesomeIcon icon={faFacebookF} />
            </a>

            <a
              className="social-icons"
              href="https://www.youtube.com/c/TheGrowingDeveloper"
            >
              <FontAwesomeIcon icon={faYoutube} />
            </a>

            <a
              className="social-icons"
              href="https://www.linkedin.com/in/singh-saheb/"
            >
              <FontAwesomeIcon icon={faLinkedinIn} />
            </a>

            <a className="social-icons" href="https://github.com/singh-saheb">
              <FontAwesomeIcon icon={faGithub} />
            </a>
          </div>
        </div>
      </Container>
    </div>
  );
}
