import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

let months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

let todayDate = new Date();

export function Calendar(props) {
  useEffect(() => {
    getCalendarCells();
  }, []);
  let [listOfCells, setList] = useState([]);
  let [currentDate,setCurrentDate] = useState(props.selectedDate);
  let endDate = new Date(
      currentDate.getFullYear(),
      currentDate.getMonth() + 1,
      0
  ).getDate();

  function Customdate(event) {
    if( document.getElementsByClassName("selected-date").length !== 0){
      document
          .getElementsByClassName("selected-date")[0]
          .classList.remove("selected-date");
    }
    document.getElementById(event.target.id).classList.add("selected-date");
    const splitDate = event.target.id.split("/");
    props.updateSelectedDate(new Date(splitDate[2],splitDate[1],splitDate[0]))
  }

  // Adding dates to calender 
  function getCalendarCells() {
    currentDate.setDate(1);
    let today = new Date();
    let day = currentDate.getDay();

    let listOfCells = [];
    for (let x = day; x > 0; x--) {
      listOfCells.push(<div className="prev_date"/>);
    }
    for (let i = 1; i <= endDate; i++) {
      if (i === today.getDate() && currentDate.getMonth() === today.getMonth())
        listOfCells.push(<div className="today selected-date">{i}</div>);

      else if (
        (i < today.getDate() && currentDate.getMonth() <= today.getMonth()) ||
        i > today.getDate() + 15
      ) 
        listOfCells.push(<div className="disabled">{i}</div>);

      else if (i < today.getDate() && currentDate.getMonth() > today.getMonth())
        listOfCells.push(<div>{i}</div>);

      else {
         let d = i+"/"+currentDate.getMonth()+"/"+currentDate.getFullYear();
        listOfCells.push(
          <div onClick={(event) => Customdate(event)} id={d}>
            {i}
          </div>
        );
      }
    }

    setList(listOfCells);
    currentDate.setDate(today.getDate())
  }

  // To set the month
  function moveDate(para) {
    if (para === "prev") {
      let newCurrentDate = new Date(currentDate.setMonth(currentDate.getMonth() - 1));
      setCurrentDate(newCurrentDate);
    } else if (para === "next") {
      let newCurrentDate = new Date(currentDate.setMonth(currentDate.getMonth() + 1));
     setCurrentDate(newCurrentDate);
      }
    getCalendarCells();
  }

  function Cal_head(params) {
    if (endDate - todayDate.getDate() < 15) {
      if (todayDate.getMonth() === currentDate.getMonth()) {
        return (
          <div className="month d-flex justify-content-between align-items-center">
            <span/>

            <div>
              <h2 id="month">{months[currentDate.getMonth()]}</h2>
            </div>

            <div className="next arrow" onClick={() => moveDate("next")}>
              <span>&#10095;</span>
            </div>
          </div>
        );
      } else {
        return (
          <div className="month d-flex justify-content-between align-items-center">
            <div className="prev arrow" onClick={() => moveDate("prev")}>
              <span>&#10094;</span>
            </div>

            <div>
              <h2 id="month">{months[currentDate.getMonth()]}</h2>
            </div>

            <span></span>
          </div>
        );
      }
    } else {
      return (
        <div className="month" style={{ justifyContent: "center" }}>
          <div>
            <h2 id="month">{months[currentDate.getMonth()]}</h2>
          </div>
        </div>
      );
    }
  }

  // Main return
  return (
    <div className="calender card">

      <Cal_head />

      <div className="weekdays">
        <div>Sun</div>
        <div>Mon</div>
        <div>Tue</div>
        <div>Wed</div>
        <div>Thu</div>
        <div>Fri</div>
        <div>Sat</div>
      </div>

      <div className="days">{listOfCells}</div>
    </div>
  );
}
