import React, {useState, useEffect} from "react";
import {Modal, Form, FormControl, CloseButton} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faWhatsapp} from "@fortawesome/free-brands-svg-icons";
import {faUserCircle, faEnvelope} from "@fortawesome/free-regular-svg-icons";
import {faClipboardList} from "@fortawesome/free-solid-svg-icons";

export function SidePanel(props) {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [data, setData] = useState(null);

    useEffect(() => {
        const requestOptions = {
            method: "GET"
        };
        fetch(
            "https://dev.thegrowingdeveloper.org/slots/getAvailableSlots",
            requestOptions
        )
            .then((response) => response.json())
            .then((jsonRes) => {
                setData(jsonRes);
                console.log(data[props.date?.getDate() + "-" + (props.date?.getMonth() + 1) + "-" + props.date?.getFullYear()])
            })
            .catch((err) => console.log(err));
    }, []);

    function submitQuery(){
        let data = {
            "name":this.name,
            "email":this.email
        }
    }

    let postDate = (props.date?.getDate()) + "-" + (props.date?.getMonth() + 1) + "-" + (props.date?.getFullYear());

    let [name,changeName] = useState("");
    let [email,chnageEmail] = useState("");
    return (
        <div>
            <Modal show={show} animation={true} centered>
                <Modal.Body>
                    <CloseButton onClick={handleClose} aria-label="Close"/>
                    <Modal.Title className="text-center">Write your query</Modal.Title>

                    <div className="appointment-form mx-4">
                        <Form>
                            <div>
                                <FontAwesomeIcon icon={faUserCircle} className="form-icon"/>
                                <FormControl placeholder="Name" value={name} onChange={(event)=>changeName(event.target.value)}/>

                            </div>

                            <div>
                                <FontAwesomeIcon icon={faEnvelope} className="form-icon"/>
                                <FormControl placeholder="Email"/>
                            </div>

                            <div>
                                <FontAwesomeIcon icon={faWhatsapp} className="form-icon"/>
                                <FormControl placeholder="Phone(WhatsApp preferred)"/>
                            </div>

                            <div>
                                <FontAwesomeIcon icon={faClipboardList} className="form-icon ta-form-icon"/>
                                <Form.Control as="textarea" rows={4} placeholder="Query"/>
                            </div>
                        </Form>
                    </div>
                </Modal.Body>
                <Modal.Footer className="form-footer">
                    <button className="proceed modal-proceed">Proceed to Payment</button>
                </Modal.Footer>
            </Modal>

            <div className="Slots">
                <h2>
                    {postDate}
                </h2>
                <div className="timings">
                    {data != null && data[postDate] != null
                        ? data[postDate][0]?.map((slot) => (
                            <button className="available-slots">
                                {slot.start_time}-{slot.end_time}
                            </button>
                        ))
                        : "No Slots available"}
                </div>
                <div>
                    <button className="proceed slot-proceed mt-3" onClick={handleShow}>
                        Proceed
                    </button>
                </div>
            </div>
        </div>
    );
}
